import React, { Component } from "react";
let homepageImage  = "https://www.digitalvidya.com/wp-content/uploads/2019/03/Homepage-Design.jpg"

class Pics extends Component {
  render() {
    return (
      <div>
        <h1>Home page</h1>
          <h2>HELLO</h2>
          <img className ="homepage-cover" src={homepageImage} alt="Logo" />
      </div>
    );
  }
}
 
export default Pics;