import "./post.css";
import Post from "./posts.js";
const ANAKIN_IMAGE = "https://upload.wikimedia.org/wikipedia/en/thumb/7/74/Anakin-Jedi.jpg/220px-Anakin-Jedi.jpg";

const RAY_IMAGE = "https://specials-images.forbesimg.com/imageserve/5e63b3c8e1e617000759130e/960x0.jpg?fit=scale";

const SHREK = "https://img.kupigolos.ru/hero/5ff0227fe8e57.jpeg?p=bh&s=8b6e1403d65ed5fdbc76310cff36dbde"
const PRINCESS = "https://lumiere-a.akamaihd.net/v1/images/header-princess-belle_9e6974f6.jpeg?region=0,0,1600,500"
function app_post() {
  return (
   <div className="content-post">
      <Post author={{
                    name: "Anakin skywalker",
                    photo: ANAKIN_IMAGE,
                    nickname: "@dart_vader"
                 }}
                 content="WTF? Who is Ray? Why she is Skywalker? Luke...?"
                 image={RAY_IMAGE}
                 date={"26 февр."}
                 comments={430}
                 shares={23}
                 likes={123}

        />

<Post author={{
                    name: "Shrek",
                    photo: SHREK,
                    nickname: "@Shrek_sweety"
                 }}
                 content="I want to find a princess, do you know where to look?"
                 image={PRINCESS}
                 date={"long time ago"}
                 comments={540}
                 shares={432}
                 likes={555}
        />
     </div>
  );
}

export default app_post;